.cards-block {
  font-size: 10px;
  line-height: 1.4;
  color: $color-black;
  margin: 0;
}

.card {
  width: $card-width;
  box-sizing: border-box;
  border-radius: 5px;
  border: 1px solid #f3f3f3;
  background: $color-white;
  padding: 32px 16px 16px;
  transition: all $animation-time;

  & * {
    box-sizing: border-box;
  }

  &:hover {
    transform: scale($scale-on-hover);

    & .card__name {
      color: $title-hovered-color;
    }
  }

  &__image {
    display: block;
    width: 160px;
    height: 134px;
    margin: 0 auto 40px;
  }

  &__name {
    font-size: 12px;
    font-weight: 500;
    line-height: 1.5;
    margin: 0 0 4px 0;
  }

  &__code {
    margin: 0;
    margin-bottom: 16px;
    color: $secondary-color;
  }

  &__review-block {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 24px;
  }

  &__reviews {
    margin: 0;
  }

  &__price-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
  }

  &__price-text {
    margin: 0;
    color: $secondary-color;
    font-size: 12px;
    line-height: 1.5;
  }

  &__price-number {
    margin: 0;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.125;
  }

  &__button-buy {
    width: 100%;
    height: 40px;
    background-color: $color-blue;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    text-transform: uppercase;
    color: $color-white;
    font-size: 14px;
    font-weight: 700;
    line-height: 1.14;
    transition: all $animation-time;

    &:hover {
      color: $color-blue;
      border: 1px solid $color-blue;
      background-color: $color-white;
    }
  }
}
